        &.bm-right-side{
          text-align: right;
          .bm-pic{
            left: calc(100% - 50px);
            right: 0;
          }

          .bm-content {
            //position: relative;
            padding-left: 0;
            padding-right: 60px;
            margin-top: -35px;

            .bm-info{
              text-align: right;

              .name{
                .bm-placeholder-wrapper{
                  margin-left: auto;
                }
              }
            }

            .bm-messages-listing {
              .bm-message {
                flex-direction: row-reverse !important;

                .bm-message-info{
                  //float: left !important;
                  .bm-time {
                    //margin-left: 0 !important;
                    //margin-right: 10px;
                  }
                }

                .message-controls {
                  flex-direction: row-reverse !important;
                  margin-left: 0;
                  margin-right: 5px;
                }
              }
            }
          }
        }

        &.bm-hide-avatars{
          .bm-content {
            //position: relative;
            padding-left: 15px;
            margin-top: 0 !important;
          }

          &.bm-right-side .bm-content {
            padding-left: 0;
            padding-right: 15px;
          }
        }


        .bm-pic{
          position: sticky;
          top: 10px;
          left: 15px;
          width: 35px;
          height: 35px;
          display: block;
          z-index: 1;

          > span{
            display: block;
            width: 100%;
            height: 100%;
          }
        }

        .bm-content {
          //position: relative;
          padding-left: 60px;
          //margin-top: -40px;
          margin-top: -35px;

          .bm-info{
            margin-bottom: 5px;

            .name{
              font-weight: bold;
              text-decoration: none;
              border: none;
              color: rgba(var(--main-bm-color), 1);
              margin-right: 5px;

              span, a{
                font-weight: bold;
                text-decoration: none;
                font-size: 13px;
                border: none;
                color: rgba( var(--main-bm-color), 1 );
                box-shadow: none;
              }
            }

            .bm-time{
              color: rgba(var(--bm-text-color), 0.5);
              font-size: 11px;
            }
          }

          .bm-messages-listing {
            margin: 0 !important;
            padding: 0 !important;
            width: 100%;
            max-width: 100%;

            .bm-message {
              position: relative;
              display: flex;
              width: auto;
              margin-bottom: 10px;
              padding: 0 0 0 0 !important;
              max-width: 100%;
              background: none;
              flex-direction: row;
              align-items: center;
              will-change: height;

              &:hover {
                background: none;
              }

              iframe{
                max-width: 100%;
              }

              .bm-message-highlight{
                position: absolute;
                background: rgba(var(--main-bm-color), 1);
              }

              .bp-messages-iframe-container {
                display: block;
                position: relative;
                overflow: hidden;
                padding-top: 56.25%;
                margin: 10px 0 0;
                padding-bottom: 3px;
                border-radius: 2px;

                > * {
                  position: absolute;
                  top: 0;
                  left: 0;
                  width: 100%;
                  height: 100%;
                  border: 0;
                  margin: 0;
                }
              }

              .bm-message-info{
                display: block;
                float: right;
                margin-left: 10px;
                user-select: none;

                &.bm-message-info-no-margin{
                  margin-left: 0;
                }

                .bm-pinned-label{
                  cursor: default;
                  color: rgba( var( --bm-text-color ), 1);
                  font-size: 11px;
                  margin-left: 5px;
                  white-space: nowrap;

                  svg{
                    vertical-align: middle;
                  }
                }

                .bm-edited-label{
                  cursor: default;
                  color: rgba( var( --bm-text-color ), 1);
                  font-size: 11px;
                  margin-left: 5px;
                  white-space: nowrap;

                  svg{
                    vertical-align: middle;
                  }
                }

                .bm-time{
                  cursor: default;
                  color: rgba( var( --bm-text-color ), 1);
                  font-size: 10px;
                  white-space: nowrap;
                }
              }


              &.selected{
                .bm-message-content{
                  &:after{
                    position: absolute;
                    left: 0;
                    right: 0;
                    top: 0;
                    bottom: 0;
                    content: "";
                    background: black;
                    opacity: 0.1;
                  }
                }
              }

              .bm-replied-message,
              .bpbm-replied-message{
                display: block;
                background: rgba( var(--main-bm-color), 0.03 );
                border-left: 2px solid rgba( var(--main-bm-color), 1 );
                padding: 5px 10px 5px 10px;
                font-size: 80%;
                white-space: nowrap;
                text-overflow: ellipsis;
                overflow: hidden;
                margin-bottom: 5px;
                user-select: none;
                cursor: pointer;


                .bm-replied-message-text .bpbm-call,
                .bpbm-replied-message-text .bpbm-call{
                  white-space: nowrap;
                  text-overflow: ellipsis;
                  overflow: hidden;
                }

                .bm-replied-message-name,
                .bpbm-replied-message-name{
                  display: block;
                  //color: rgba( var(--main-bm-color), 1 );
                  font-weight: bold;
                  margin-bottom: 5px;
                }

                video{
                  max-height: 130px !important;
                }

                .bpbm-sticker{
                  img{
                    max-height: 130px;
                    width: auto !important;
                  }
                }

                .videos .video,
                .images{
                  padding: 0 !important;
                }
              }

              .bm-message-content {
                position: relative;
                //background: #e8e8e8;
                color: rgba(var(--bm-text-color), 1);

                padding: 7px 12px;
                max-width: 90%;
                word-break: break-all;
                font-size: var(--bm-message-font-size);
                line-height: var(--bm-message-line-height);
                text-align: left;
                //overflow: hidden;

                a{
                  color: rgba(var(--bm-text-color), 1);
                  text-decoration: underline !important;
                }

                &.favorited-page{
                  cursor: pointer;
                }

                &.bm-message-pending{
                  opacity: 0.5;

                  &:hover{
                    opacity: 1;
                  }
                }

                &.no-padding{
                  padding: 0 !important;
                }

                &.bm-hide-background{
                  background: none;
                  padding: 0 !important;

                  .bm-message-info .bm-time {
                    color: gray !important;
                  }
                }

                &.has-iframe {
                  width: 100%;
                  max-width: 600px !important;
                }

                &.voice-message {
                  width: 100%;
                  max-width: 600px;

                  &+.message-controls{
                    margin-top: -15px;
                  }
                }

                .bm-attachments-space-hack{
                  width: 100%;
                  display: block;
                  min-width: 300px;
                }

                .bm-message-content-text{
                  word-break: break-word;
                  white-space: normal;

                  p{
                    display: inline;
                    word-break: break-word;
                    line-height: unset;
                    word-wrap: unset;
                    margin: 0 !important;
                    padding: 0 !important;
                  }

                  h1{
                    margin: 0 !important;
                    padding: 0 !important;
                    font-size: 20px;
                  }


                  h2{
                    margin: 0 !important;
                    padding: 0 !important;
                    font-size: 18px;
                  }

                  h3{
                    margin: 0 !important;
                    padding: 0 !important;
                    font-size: 16px;
                  }

                  .bp-messages-iframe-container{
                    display: block;
                    position: relative;
                    overflow: hidden;
                    padding-top: 56.25%;
                    margin: 10px 0 0;
                  }
                }

                .bm-voice-message{
                  display: flex;
                  flex-wrap: nowrap;
                  align-content: center;
                  justify-content: flex-start;
                  align-items: center;
                  margin-top: 5px;
                  min-height: 30px;

                  .bm-voice-message-duration{
                    position: absolute;
                    bottom: 0;
                    color: gray !important;
                    font-size: 11px;
                    white-space: nowrap;
                    left: 35px;
                    cursor: default;
                    display: flex;
                    align-items: center;

                    .bm-voice-transcribe-btn {
                      display: inline-flex;
                      align-items: center;
                      gap: 2px;
                      cursor: pointer;
                      margin-left: 5px;
                      &:hover { opacity: 0.7; }
                    }

                    .bm-voice-transcribing {
                      font-style: italic;
                      margin-left: 5px;
                    }
                  }

                  .bm-voice-message-play{
                    width: 35px;
                    font-size: 30px;
                    text-align: left;
                    cursor: pointer;
                    color: rgba( var(--main-bm-color), 1 );
                  }

                  .bm-waveform{
                    width: 80%;
                    width: calc(100% - 35px);

                    wave{
                      //border-right-color: rgba( var(--main-bm-color), 1 ) !important;
                      max-width: 100%;
                      overflow: hidden !important;
                    }
                  }
                }

                .bm-voice-transcription-text {
                  color: gray;
                  font-size: 13px;
                  padding-left: 35px;
                  word-wrap: break-word;
                  white-space: pre-wrap;
                  line-height: 1.4;
                  margin-top: 10px;

                  .bm-voice-transcription-icon {
                    display: inline-flex;
                    align-items: center;
                    vertical-align: text-bottom;

                    svg {
                      vertical-align: middle;
                    }
                  }
                }

                .bpbm-sticker {
                  max-width: 200px;
                  width: 100%;
                  display: block;
                }

                .bpbm-sticker img {
                  width: 100%;
                  height: auto;
                  display: block;
                  max-width: 100%;
                }

                .bm-mention{
                  display: inline-flex;
                  align-items: center;
                  flex-direction: row;
                  background: rgb(0 0 0 / 10%);
                  padding: 2px 5px;
                  vertical-align: middle;

                  &.bm-mention-current{
                    font-weight: bold;
                  }

                  > .avatar{
                    margin-right: 5px;
                  }
                }

                .bm-reactions{
                  display: flex;
                  margin-top: 4px;
                  flex-direction: row;
                  align-items: center;
                  justify-content: flex-start;

                  .bm-reaction {
                    display: flex;
                    flex-direction: row;
                    flex-wrap: nowrap;
                    align-content: center;
                    justify-content: flex-start;
                    align-items: center;
                    margin-right: 5px;

                    img {
                      height: 13px !important;
                      width: 13px !important;
                    }

                    .bm-reaction-count {
                      font-size: 11px;
                      margin-left: 2px;
                    }
                  }
                }

                .bm-attachments{
                  width: 100%;
                  //min-width: 300px;

                  .bm-images{
                    overflow: hidden;
                    a{
                      //position: absolute;
                      max-height: 300px;
                      max-width: 300px;
                      overflow: hidden;
                      margin-bottom: 5px;
                      //margin-left: auto;
                      //margin-right: auto;

                      img{
                        opacity: 0;
                        width: 100%;
                        height: 100%;
                      }
                    }
                  }

                  .bm-videos{
                    overflow: hidden;

                    .bm-video{
                      max-width: 100%;
                      max-height: 300px;
                    }
                  }

                  .bm-audios{
                    overflow: hidden;

                    .bm-audio{
                      max-width: 100%;
                      height: 30px;
                    }
                  }

                  .bm-files{
                    .bm-file{
                      border: 1px solid rgba(var(--bm-text-color), 1);
                      display: block;
                      overflow: hidden;
                      border-radius: 4px;
                      -moz-transition: border-color .2s ease-in-out,-moz-box-shadow .2s ease-in-out;
                      -o-transition: border-color .2s ease-in-out,box-shadow .2s ease-in-out;
                      -webkit-transition: border-color .2s ease-in-out,-webkit-box-shadow .2s ease-in-out;
                      transition: border-color .2s ease-in-out,box-shadow .2s ease-in-out;
                      width: 100%;
                      text-decoration: none;
                      margin: 2px 0;
                      padding: 5px 12px;
                      outline: 0;
                      color: inherit !important;
                      box-sizing: border-box;
                      align-items: center;
                      white-space: normal;

                      svg {
                        margin-right: 5px;
                      }

                      .size{
                        margin-left: 5px;
                      }
                    }
                  }
                }

                .url-wrap {
                  border: 1px solid rgba(var(--bm-text-color), 1);
                  overflow: hidden;
                  border-radius: 4px;
                  -moz-transition: border-color 0.2s ease-in-out, -moz-box-shadow 0.2s ease-in-out;
                  -o-transition: border-color 0.2s ease-in-out, box-shadow 0.2s ease-in-out;
                  -webkit-transition: border-color 0.2s ease-in-out, -webkit-box-shadow 0.2s ease-in-out;
                  transition: border-color 0.2s ease-in-out, box-shadow 0.2s ease-in-out;
                  display: block;
                  width: 100%;
                  text-decoration: none;
                  margin: 5px 0;
                  outline: 0;
                  color: rgba(var(--bm-text-color), 1);

                  *{
                    border-color: rgba(var(--bm-text-color), 1);
                    color: rgba(var(--bm-text-color), 1);
                  }

                  + br {
                    display: none;
                  }

                  .url-image {
                    display: block;
                    box-shadow: none;
                    border-radius: 0;
                    margin: 0;
                    display: block;
                    height: 150px;
                    width: 100%;
                    background-size: cover;
                    background-repeat: no-repeat;
                    background-position: center;
                  }

                  .url-image + .url-description {
                    border-top: 1px solid #fafbfc;
                  }

                  .url-description {
                    padding: 15px;
                    display: block;

                    .url-title {
                      display: block;
                      color:  rgba(var(--bm-text-color), 1) !important;
                      font-size: 120%;
                      line-height: 100%;
                    }
                  }

                  .url-title i {
                    font-size: 75%;
                  }

                  .url-site {
                    display: block;
                    font-size: 80%;
                    margin: 6px 0 0px;
                  }
                }

              }

              .message-controls{
                display: flex;
                margin-left: 5px;
                font-size: 13px;

                > span{
                  position: relative;
                  font-size: 15px;
                  visibility: hidden;
                  margin: 0 5px;
                  width: 15px;
                  height: 15px;
                  cursor: pointer;
                  color: gray;
                  text-align: center;

                  > svg{
                    position: absolute;
                    bottom: 0;
                    top: 0;
                    left: 0;
                    right: 0;
                    margin: auto;
                  }

                  &.bm-status{
                    visibility: visible;
                    cursor: default;

                    &[data-status="seen"]{
                      color: rgba( var(--main-bm-color), 1 );
                    }
                  }

                  &.favorite{

                    color: #fdae00 !important;

                    i:before {
                      content: "";
                      font-weight: 400;
                    }

                    &.active {
                      visibility: visible;
                      i:before{
                        font-weight: bold;
                      }
                    }
                  }
                }
              }

              &.selected, &:hover{
                .message-controls{
                  > span {
                    visibility: visible;
                  }
                }
              }

              .bpbm-gif {
                display: block;
                border-radius: 2px;
                overflow: hidden;

                video{
                  height: auto;
                  //width: auto;
                }
              }

              .bm-attachments {
                padding: 4px 0;

                > * + * {
                  margin-top: 5px;
                }
              }

              .videos {
                .video {
                  padding: 0;

                  video {
                    border-radius: 2px;
                    overflow: hidden;
                  }

                  & + .video {
                    padding-top: 5px;
                  }
                }
              }

              .images {
                padding: 3px 0;

                a {
                  display: block;
                  width: auto;
                  height: auto;
                  background: none !important;
                  padding-bottom: 0;
                  border: 0;
                  margin: 0;
                  border-radius: 2px;
                  overflow: hidden;
                  max-width: 300px;

                  & + a {
                    padding-top: 10px;
                  }

                  img {
                    display: block !important;
                  }

                }
              }
            }
          }
        }
      }

    }

    &.bm-template-standard{
      .bm-list .bm-messages-stack {
        .bm-content {
          .bm-messages-listing {
            .bm-message {
              //display: block !important;
              max-width: calc(100% - 15px) !important;
              align-items: flex-start !important;
              margin: -5px -5px 5px !important;

              &.selected .bm-message-content{
                border-radius: 3px !important;
                overflow: hidden;
              }

              .bpbm-gif{
                display: inline-block;

                video{
                  width: auto;
                  display: inline-block;
                }
              }

              .bm-message-content {
                width: 100% !important;
                max-width: 100% !important;
                background: none !important;
                padding: 5px !important;

                .bm-voice-message {
                  margin-top: 0 !important;
                }

                &.voice-message {
                  max-width: initial !important;
                  width: 100% !important;
                }
              }

              .bm-message-info {
                line-height: 1 !important;

                .bm-time {
                  margin-left: 0 !important;
                }
              }

              .message-controls {
                float: right;
                flex-direction: row-reverse;
              }
            }
          }
        }
      }
    }
  }

  .chat-header{
    display: flex;
    position: relative;
    height: 44px;
    min-height: 44px;
    background: rgba( var(--bm-bg-secondary), 1 );
    border-bottom: 1px solid rgba(var(--bm-border-secondary-color), 1);
    line-height: 42px;
    box-sizing: border-box;
    align-items: center;
    flex-direction: row;
    justify-content: flex-start;
    z-index: 2;
    font-size: 1.1rem;

    > .bm-btn{
      position: relative;
      width: 50px;
      text-align: center;
      height: 42px;
      line-height: 42px;
      text-decoration: none;
      cursor: pointer;
    }

    .thread-info{
      display: inline-flex;
      line-height: 15px;
      font-size: 14px;
      margin-right: auto !important;
      align-items: center;
      margin-left: 5px !important;
      margin-bottom: 0 !important;
      margin-top: 0 !important;
      width: 100%;
      min-width: 0;
      overflow: hidden;
      cursor: pointer;

      .bm-thread-emoji{
        font-size: 25px;
        line-height: 30px;
      }

      .avatar{
        display: block;
        margin-right: 8px;
        height: 30px;
        width: 30px;
        min-width: 30px;

        a, img{
          margin: 0;
          display: block;
        }
      }


      .avatar-group{
        position: relative;
        width: 34px;
        display: grid;
        grid-template-columns: 15px 15px;
        grid-template-rows: 15px 15px;
        grid-gap: 2px 2px;
        margin-right: 6px;

        &.avatars-count-2{
          grid-template-rows: 15px;
        }

        .avatar{
          width: 15px;
          height: 15px;
          min-width: 15px;
          margin-right: 0;
        }
      }

      .thread-info-data{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: flex-start;
        align-content: flex-start;
        max-width: 100%;
        max-width: calc(100% - 30px);

        .name{
          margin-bottom: 2px;
          white-space: nowrap;
          text-overflow: ellipsis;
          max-width: 100%;
          overflow: hidden;

          a{
            color: rgba(var(--bm-text-color), 1);
            line-height: 1;
          }
        }

        .sub-name{
          font-size: 12px;
          line-height: 14px;
          min-height: 14px;
          text-overflow: ellipsis;
          max-width: 100%;
          overflow: hidden;
          white-space: nowrap;

          .group-online{
            text-overflow: ellipsis;
            max-width: 100%;
            overflow: hidden;
            white-space: nowrap;
          }


          .bm-thread-sync{
            display: flex;
            align-items: center;
            justify-content: flex-start;

            i{
              font-size: 10px;
              vertical-align: middle;
              margin-right: 5px;
            }
          }
        }

        .group-online,
        .last-online{

          &.online-now{
            color: green;
          }
        }

      }
    }
  }

  .new-thread{
    display: flex;
    flex-direction: column;
    height: 100%;
    width: 100%;
    padding: 20px;

    .bm-reply{
      margin-top: auto;
    }
  }

  .bm-reply{
    position: relative;
    display: flex;
    background: rgba( var(--bm-bg-color), 1 );
    border-top: 1px solid rgba( var(--bm-border-color), 0.62 );
    //align-items: flex-end;
    width: 100%;
    left: 0;
    right: 0;
    bottom: 0;
    top: auto;
    z-index: 10;
    //min-height: 50px;
    //overflow: hidden;


    > * {
      position: relative;
      background: rgba( var(--bm-bg-color), 1 );
      z-index: 5;
    }

    .bm-reply-restricted{
      width: 100%;
      text-align: center;
      font-size: 16px;
      margin: 10px 10px;
      white-space: normal;
      background: rgba(var(--main-bm-color), 0.05);
      border: 1px solid rgba(var(--main-bm-color), 0.1);
      overflow: hidden;
      border-radius: 3px;
      padding: 12px 10px;

      a{
        text-decoration: underline !important;
      }
    }

    .bm-attachment{
      position: relative;
      display: flex;
      min-width: 50px;
      height: 100%;
      text-align: center;
      font-size: 24px;
      color: rgba( var(--bm-text-color), 0.36 );
      cursor: pointer;
      align-items: center;
      justify-content: center;

      &.bm-attachment-progress{
        font-size: 16px;
      }

      .bm-attachment-progress-bar {
        display: flex;
        justify-content: center;
