// Broadcast mode styles (shared between audio and video group calls)
.bp-messages-call-wrap{
  // Hand raised indicator (video grid)
  .bpbm-hand-raised-status{
    text-align: right;
    position: absolute;
    right: 0;
    top: 0;
    padding: 5px;
    z-index: 2;

    .bpbm-hand-raised{
      display: inline-flex;
      align-items: center;
      justify-content: center;
      width: 24px;
      height: 24px;
      background: rgba(255, 193, 7, 0.9);
      border-radius: 50%;
      color: #000;
      font-size: 14px;
      animation: bpbm-hand-pulse 1.5s ease-in-out infinite;
    }
  }

  // Hand raised indicator (audio grid)
  .bpbm-caller-actions{
    .bpbm-hand-raised{
      display: inline-flex;
      align-items: center;
      justify-content: center;
      width: 30px;
      height: 30px;
      background: rgba(255, 193, 7, 0.9);
      border-radius: 50%;
      color: #000;
      font-size: 14px;
      line-height: 30px;
      animation: bpbm-hand-pulse 1.5s ease-in-out infinite;
    }
  }

  @keyframes bpbm-hand-pulse {
    0%, 100% { transform: scale(1); }
    50% { transform: scale(1.15); }
  }

  // Viewer badge (video grid)
  .bp-messages-group-call-input-video-inner{
    .bpbm-viewer-badge{
      position: absolute;
      bottom: 28px;
      right: 5px;
      z-index: 2;

      > span{
        display: inline-block;
        padding: 2px 8px;
        background: rgba(0, 0, 0, 0.6);
        color: rgba(255, 255, 255, 0.8);
        border-radius: 3px;
        font-size: 10px;
        text-transform: uppercase;
        letter-spacing: 0.5px;
      }
    }
  }

  // Viewer badge (audio grid)
  .bp-messages-group-call-input-audio-inner{
    .bpbm-viewer-badge{
      display: inline-block;
      padding: 2px 8px;
      background: rgba(0, 0, 0, 0.1);
      color: rgba(var(--bm-text-color), 0.6);
      border-radius: 3px;
      font-size: 10px;
      text-transform: uppercase;
      letter-spacing: 0.5px;
      margin-left: 5px;
    }
  }

  // Promote button highlighted (hand raised)
  .bpbm-promote-highlighted{
    background: rgba(255, 193, 7, 0.9) !important;
    color: #000 !important;
    animation: bpbm-hand-pulse 1.5s ease-in-out infinite;
  }
}

