.bm-smart-container{

  .bm-draggable-handle{
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    opacity: 0;
    cursor: move;
    z-index: 2;
  }

  .bm-resizable-handle{
    position: absolute;
    width: 15px;
    height: 15px;
    opacity: 0;
    z-index: 3;

    &.bm-resizable-handle-top-left{
        top: 0;
        left: 0;
        cursor: nw-resize;
    }

    &.bm-resizable-handle-top-right{
        top: 0;
        right: 0;
        cursor: ne-resize;
    }
    &.bm-resizable-handle-bottom-left{
        bottom: 0;
        left: 0;
        cursor: sw-resize;
    }
    &.bm-resizable-handle-bottom-right{
        bottom: 0;
        right: 0;
        cursor: se-resize;
    }
  }
}

