/**
* General Uppy styles that apply to everything inside the .uppy-Root container
*/
.uppy-Root {
    position: relative;
    box-sizing: border-box;
    color: rgba(var(--bm-text-color), 1);
    font-family: -apple-system, system-ui, BlinkMacSystemFont, "Segoe UI", "Segoe UI Symbol", "Segoe UI Emoji", "Apple Color Emoji", Roboto, Helvetica, Arial, sans-serif;
    line-height: 1;
    text-align: left;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

[dir=rtl] .uppy-Root,
.uppy-Root[dir=rtl] {
    text-align: right;
}

.uppy-Root *,
.uppy-Root *::before,
.uppy-Root *::after {
    box-sizing: inherit;
}

.uppy-Root [hidden] {
    display: none;
}

.uppy-u-reset {
    all: initial;
    box-sizing: border-box;
    -webkit-appearance: none;
    appearance: none;
    font-family: -apple-system, system-ui, BlinkMacSystemFont, "Segoe UI", "Segoe UI Symbol", "Segoe UI Emoji", "Apple Color Emoji", Roboto, Helvetica, Arial, sans-serif;
    line-height: 1;
}

[dir=rtl] .uppy-u-reset {
    text-align: right;
}

.uppy-c-textInput {
    font-family: inherit;
    font-size: 14px;
    line-height: 1.5;
    padding: 6px 8px;
    color: rgba(var(--bm-text-color), 1) !important;
    background-color: rgba(var(--bm-bg-color), 1);
    border: 1px solid rgba(var(--bm-border-color), 1);
    border-radius: 4px;
}
.uppy-size--md .uppy-c-textInput {
    padding: 8px 10px;
}
.uppy-c-textInput:focus {
    border-color: rgba(var(--main-bm-color), 0.6);
    outline: none;
    box-shadow: 0 0 0 3px rgba(var(--main-bm-color), 0.15);
}

.uppy-c-icon {
    display: inline-block;
    max-width: 100%;
    max-height: 100%;
    overflow: hidden;
    fill: currentColor;
}

.uppy-c-btn {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    font-weight: 500;
    font-size: inherit;
    font-family: inherit;
    color: inherit;
    line-height: 1;
    white-space: nowrap;
    text-align: center;
    transition-duration: 0.3s;
    transition-property: background-color, color;
    -webkit-user-select: none;
    user-select: none;
}
[dir=rtl] .uppy-c-btn {
    text-align: center;
}

.uppy-c-btn:not(:disabled):not(.disabled) {
    cursor: pointer;
}

.uppy-c-btn::-moz-focus-inner {
    border: 0;
}

.uppy-c-btn-primary {
    padding: 10px 18px;
    color: #fff;
    font-size: 14px;
    background-color: rgba(var(--main-bm-color), 1);
    border-radius: 4px;
}
.uppy-c-btn-primary:hover {
    background-color: rgba(var(--main-bm-color), 0.8);
}
.uppy-c-btn-primary:focus {
    outline: none;
    box-shadow: 0 0 0 3px rgba(var(--main-bm-color), 0.4);
}
.uppy-size--md .uppy-c-btn-primary {
    padding: 13px 22px;
}

.uppy-c-btn-link {
    padding: 10px 15px;
    font-size: 14px;
    line-height: 1;
    background-color: transparent;
    border-radius: 4px;
}

.uppy-c-btn-link:focus {
    outline: none;
    box-shadow: 0 0 0 3px rgba(var(--main-bm-color), 0.25);
}
.uppy-size--md .uppy-c-btn-link {
    padding: 13px 18px;
}

.uppy-Informer {
    position: absolute;
    right: 0;
    bottom: 60px;
    left: 0;
    z-index: 1005;
    text-align: center;
}
.uppy-Informer span > div {
    margin-bottom: 6px;
}

.uppy-Informer-animated {
    z-index: -1000;
    transform: translateY(350%);
    opacity: 0;
    transition: all 300ms ease-in;
}

.uppy-Informer p {
    display: inline-block;
    max-width: 90%;
    margin: 0;
    padding: 0;
    padding: 6px 15px;
    color: #fff;
    font-weight: 400;
    font-size: 12px;
    line-height: 1.4;
    background-color: #757575;
    border-radius: 18px;
}
.uppy-size--md .uppy-Informer p {
    max-width: 500px;
    padding: 10px 20px;
    font-size: 14px;
    line-height: 1.3;
}

[dir="ltr"] .uppy-Informer p span{
    left: 3px;
}

[dir="rtl"] .uppy-Informer p span{
    right: 3px;
}

[dir="ltr"] .uppy-Informer p span{
    margin-left: -1px;
}

[dir="rtl"] .uppy-Informer p span{
    margin-right: -1px;
}

.uppy-Informer p span {
    position: relative;
    top: -1px;
    display: inline-block;
    width: 13px;
    height: 13px;
    color: rgba(var(--bm-text-color), 0.7);
    font-size: 10px;
    line-height: 12px;
    vertical-align: middle;
    background-color: rgba(var(--bm-bg-color), 1);
    border-radius: 50%;
}

.uppy-Informer p span:hover {
    cursor: help;
}

.uppy-Informer p span::after {
    line-height: 1.3;
    word-wrap: break-word;
}

/* -------------------------------------------------------------------
  Microtip

  Modern, lightweight css-only tooltips
  Just 1kb minified and gzipped

  @author Ghosh
  @package Microtip

----------------------------------------------------------------------
  1. Base Styles
  2. Direction Modifiers
  3. Position Modifiers
-------------------------------------------------------------------- */
/* ------------------------------------------------
  [1] Base Styles
------------------------------------------------- */
.uppy-Root [aria-label][role~=tooltip] {
    position: relative;
}

.uppy-Root [aria-label][role~=tooltip]::before,
.uppy-Root [aria-label][role~=tooltip]::after {
    position: absolute;
    z-index: 10;
    box-sizing: border-box;
    transform: translate3d(0, 0, 0);
    transform-origin: top;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    opacity: 0;
    transition: all var(--microtip-transition-duration, 0.18s) var(--microtip-transition-easing, ease-in-out) var(--microtip-transition-delay, 0s);
    pointer-events: none;
    will-change: transform;
}

.uppy-Root [aria-label][role~=tooltip]::before {
    background-size: 100% auto !important;
    content: "";
}

.uppy-Root [aria-label][role~=tooltip]::after {
    box-sizing: content-box;
    padding: 0.5em 1em;
    color: #fff;
    font-weight: var(--microtip-font-weight, normal);
    font-size: var(--microtip-font-size, 13px);
    white-space: nowrap;
    text-transform: var(--microtip-text-transform, none);
    background: rgba(17, 17, 17, 0.9);
    border-radius: 4px;
    content: attr(aria-label);
}

.uppy-Root [aria-label][role~=tooltip]:hover::before,
.uppy-Root [aria-label][role~=tooltip]:hover::after,
.uppy-Root [aria-label][role~=tooltip]:focus::before,
.uppy-Root [aria-label][role~=tooltip]:focus::after {
    opacity: 1;
    pointer-events: auto;
}

/* ------------------------------------------------
  [2] Position Modifiers
------------------------------------------------- */
.uppy-Root [role~=tooltip][data-microtip-position|=top]::before {
    bottom: 100%;
    left: 50%;
    width: 18px;
    height: 6px;
    margin-bottom: 5px;
    background: url("data:image/svg+xml;charset=utf-8,%3Csvg%20xmlns%3D%22http%3A//www.w3.org/2000/svg%22%20width%3D%2236px%22%20height%3D%2212px%22%3E%3Cpath%20fill%3D%22rgba%2817,%2017,%2017,%200.9%29%22%20transform%3D%22rotate%280%29%22%20d%3D%22M2.658,0.000%20C-13.615,0.000%2050.938,0.000%2034.662,0.000%20C28.662,0.000%2023.035,12.002%2018.660,12.002%20C14.285,12.002%208.594,0.000%202.658,0.000%20Z%22/%3E%3C/svg%3E") no-repeat;
    transform: translate3d(-50%, 0, 0);
}

.uppy-Root [role~=tooltip][data-microtip-position|=top]::after {
    bottom: 100%;
    left: 50%;
    margin-bottom: 11px;
    transform: translate3d(-50%, 0, 0);
}

.uppy-Root [role~=tooltip][data-microtip-position|=top]:hover::before {
    transform: translate3d(-50%, -5px, 0);
}

.uppy-Root [role~=tooltip][data-microtip-position=top]:hover::after {
    transform: translate3d(-50%, -5px, 0);
}

/* ------------------------------------------------
  [2.1] Top Left
------------------------------------------------- */
.uppy-Root [role~=tooltip][data-microtip-position=top-left]::after {
    bottom: 100%;
    transform: translate3d(calc(-100% + 16px), 0, 0);
}

.uppy-Root [role~=tooltip][data-microtip-position=top-left]:hover::after {
    transform: translate3d(calc(-100% + 16px), -5px, 0);
}

/* ------------------------------------------------
  [2.2] Top Right
------------------------------------------------- */
.uppy-Root [role~=tooltip][data-microtip-position=top-right]::after {
    bottom: 100%;
    transform: translate3d(calc(0% - 16px), 0, 0);
}

.uppy-Root [role~=tooltip][data-microtip-position=top-right]:hover::after {
    transform: translate3d(calc(0% - 16px), -5px, 0);
}

/* ------------------------------------------------
  [2.3] Bottom
------------------------------------------------- */
.uppy-Root [role~=tooltip][data-microtip-position|=bottom]::before {
    top: 100%;
    bottom: auto;
    left: 50%;
    width: 18px;
    height: 6px;
    margin-top: 5px;
    margin-bottom: 0;
    background: url("data:image/svg+xml;charset=utf-8,%3Csvg%20xmlns%3D%22http%3A//www.w3.org/2000/svg%22%20width%3D%2236px%22%20height%3D%2212px%22%3E%3Cpath%20fill%3D%22rgba%2817,%2017,%2017,%200.9%29%22%20transform%3D%22rotate%28180%2018%206%29%22%20d%3D%22M2.658,0.000%20C-13.615,0.000%2050.938,0.000%2034.662,0.000%20C28.662,0.000%2023.035,12.002%2018.660,12.002%20C14.285,12.002%208.594,0.000%202.658,0.000%20Z%22/%3E%3C/svg%3E") no-repeat;
    transform: translate3d(-50%, -10px, 0);
}

.uppy-Root [role~=tooltip][data-microtip-position|=bottom]::after {
    top: 100%;
    left: 50%;
    margin-top: 11px;
    transform: translate3d(-50%, -10px, 0);
}

.uppy-Root [role~=tooltip][data-microtip-position|=bottom]:hover::before {
    transform: translate3d(-50%, 0, 0);
}

.uppy-Root [role~=tooltip][data-microtip-position=bottom]:hover::after {
    transform: translate3d(-50%, 0, 0);
}

/* ------------------------------------------------
  [2.4] Bottom Left
------------------------------------------------- */
.uppy-Root [role~=tooltip][data-microtip-position=bottom-left]::after {
    top: 100%;
    transform: translate3d(calc(-100% + 16px), -10px, 0);
}

.uppy-Root [role~=tooltip][data-microtip-position=bottom-left]:hover::after {
    transform: translate3d(calc(-100% + 16px), 0, 0);
}

/* ------------------------------------------------
  [2.5] Bottom Right
------------------------------------------------- */
.uppy-Root [role~=tooltip][data-microtip-position=bottom-right]::after {
    top: 100%;
    transform: translate3d(calc(0% - 16px), -10px, 0);
}

.uppy-Root [role~=tooltip][data-microtip-position=bottom-right]:hover::after {
    transform: translate3d(calc(0% - 16px), 0, 0);
}

/* ------------------------------------------------
  [2.6] Left
------------------------------------------------- */
.uppy-Root [role~=tooltip][data-microtip-position=left]::before,
.uppy-Root [role~=tooltip][data-microtip-position=left]::after {
    top: 50%;
    right: 100%;
    bottom: auto;
    left: auto;
    transform: translate3d(10px, -50%, 0);
}

.uppy-Root [role~=tooltip][data-microtip-position=left]::before {
    width: 6px;
    height: 18px;
    margin-right: 5px;
    margin-bottom: 0;
    background: url("data:image/svg+xml;charset=utf-8,%3Csvg%20xmlns%3D%22http%3A//www.w3.org/2000/svg%22%20width%3D%2212px%22%20height%3D%2236px%22%3E%3Cpath%20fill%3D%22rgba%2817,%2017,%2017,%200.9%29%22%20transform%3D%22rotate%28-90%2018%2018%29%22%20d%3D%22M2.658,0.000%20C-13.615,0.000%2050.938,0.000%2034.662,0.000%20C28.662,0.000%2023.035,12.002%2018.660,12.002%20C14.285,12.002%208.594,0.000%202.658,0.000%20Z%22/%3E%3C/svg%3E") no-repeat;
}

.uppy-Root [role~=tooltip][data-microtip-position=left]::after {
    margin-right: 11px;
}

.uppy-Root [role~=tooltip][data-microtip-position=left]:hover::before,
.uppy-Root [role~=tooltip][data-microtip-position=left]:hover::after {
    transform: translate3d(0, -50%, 0);
}

/* ------------------------------------------------
  [2.7] Right
------------------------------------------------- */
.uppy-Root [role~=tooltip][data-microtip-position=right]::before,
.uppy-Root [role~=tooltip][data-microtip-position=right]::after {
    top: 50%;
    bottom: auto;
    left: 100%;
    transform: translate3d(-10px, -50%, 0);
}

.uppy-Root [role~=tooltip][data-microtip-position=right]::before {
    width: 6px;
    height: 18px;
    margin-bottom: 0;
    margin-left: 5px;
    background: url("data:image/svg+xml;charset=utf-8,%3Csvg%20xmlns%3D%22http%3A//www.w3.org/2000/svg%22%20width%3D%2212px%22%20height%3D%2236px%22%3E%3Cpath%20fill%3D%22rgba%2817,%2017,%2017,%200.9%29%22%20transform%3D%22rotate%2890%206%206%29%22%20d%3D%22M2.658,0.000%20C-13.615,0.000%2050.938,0.000%2034.662,0.000%20C28.662,0.000%2023.035,12.002%2018.660,12.002%20C14.285,12.002%208.594,0.000%202.658,0.000%20Z%22/%3E%3C/svg%3E") no-repeat;
}

.uppy-Root [role~=tooltip][data-microtip-position=right]::after {
    margin-left: 11px;
}

.uppy-Root [role~=tooltip][data-microtip-position=right]:hover::before,
.uppy-Root [role~=tooltip][data-microtip-position=right]:hover::after {
    transform: translate3d(0, -50%, 0);
}

/* ------------------------------------------------
  [3] Size
------------------------------------------------- */
.uppy-Root [role~=tooltip][data-microtip-size=small]::after {
    width: 80px;
    white-space: initial;
}

.uppy-Root [role~=tooltip][data-microtip-size=medium]::after {
    width: 150px;
    white-space: initial;
}

.uppy-Root [role~=tooltip][data-microtip-size=large]::after {
    width: 260px;
    white-space: initial;
}

.uppy-StatusBar {
    position: relative;
    z-index: 1001;
    display: flex;
    height: 46px;
    font-weight: 400;
    font-size: 12px;
    line-height: 40px;
    transition: height 0.2s;
    color: rgba( var( --bm-text-color ), 1 );
    background-color: rgba( var( --bm-bg-secondary ), 1 );
}

.uppy-StatusBar::before {
    background-color: rgba( var(--bm-border-color), 1 );
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 2px;
    background-color: rgba(var(--bm-border-color), 1);
    content: "";
}

.uppy-StatusBar[aria-hidden=true] {
    height: 0;
    overflow-y: hidden;
}

.uppy-StatusBar.is-complete .uppy-StatusBar-progress {
    background-color: #1bb240;
}

.uppy-StatusBar.is-error .uppy-StatusBar-progress {
    background-color: #e32437;
}

.uppy-StatusBar.is-complete .uppy-StatusBar-statusIndicator {
    color: #1bb240;
}

.uppy-StatusBar.is-error .uppy-StatusBar-statusIndicator {
    color: #e32437;
}

.uppy-StatusBar:not([aria-hidden=true]).is-waiting {
    height: 65px;
    background-color: rgba(var(--bm-bg-color), 1);
    border-top: 1px solid rgba( var(--bm-border-color), 1 );
}

.uppy-StatusBar-progress {
    position: absolute;
    z-index: 1001;
    height: 2px;
    background-color: rgba(var(--main-bm-color), 1);
    transition: background-color, width 0.3s ease-out;
}
.uppy-StatusBar-progress.is-indeterminate {
    background-image: linear-gradient(45deg, rgba(0, 0, 0, 0.3) 25%, transparent 25%, transparent 50%, rgba(0, 0, 0, 0.3) 50%, rgba(0, 0, 0, 0.3) 75%, transparent 75%, transparent);
    background-size: 64px 64px;
    animation: uppy-StatusBar-ProgressStripes 1s linear infinite;
}

@keyframes uppy-StatusBar-ProgressStripes {
    from {
        background-position: 0 0;
    }
    to {
        background-position: 64px 0;
    }
}
.uppy-StatusBar.is-preprocessing .uppy-StatusBar-progress,
.uppy-StatusBar.is-postprocessing .uppy-StatusBar-progress {
    background-color: #f6a623;
}

.uppy-StatusBar.is-waiting .uppy-StatusBar-progress {
    display: none;
}

