.uppy-size--lg .uppy-Webcam-videoSource-select {
    margin-bottom: 0;
    font-size: 14px;
}

.uppy-Webcam-videoSource-select::-ms-expand {
    display: none;
}

.uppy-Webcam-buttonContainer {
    width: 50%;
    margin-left: 25%;
    text-align: center;
}

.uppy-size--lg .uppy-Webcam-buttonContainer {
    width: 34%;
    margin-left: 0;
}

.uppy-Webcam-recordingLength {
    flex-grow: 0;
    width: 25%;
    color: #757575;
    font-family: SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
    text-align: right;
}

.uppy-size--lg .uppy-Webcam-recordingLength {
    width: 33%;
}

.uppy-Webcam-button {
    width: 45px;
    height: 45px;
    color: #fff;
    background-color: #e32437;
    border-radius: 50%;
    cursor: pointer;
    transition: all 0.3s;
}
.uppy-Webcam-button:focus {
    outline: none;
}
.uppy-Webcam-button::-moz-focus-inner {
    border: 0;
}
.uppy-Webcam-button:focus {
    box-shadow: 0 0 0 3px rgba(var(--main-bm-color), 0.5);
}
.uppy-Webcam-button:hover {
    background-color: #d31b2d;
}
.uppy-Webcam-button--submit {
    margin: 0 12px;
    background-color: #1bb240;
}
.uppy-Webcam-button--submit:hover {
    background-color: #189c38;
}

.uppy-Webcam-button svg {
    display: inline-block;
    width: 30px;
    max-width: 100%;
    height: 30px;
    max-height: 100%;
    overflow: hidden;
    vertical-align: text-top;
    fill: currentColor;
}

.uppy-size--md .uppy-Webcam-button {
    width: 60px;
    height: 60px;
}

.uppy-Webcam-button--picture {
    margin-right: 12px;
}

.uppy-Webcam-permissons {
