    align-items: center;
    justify-content: center;
    height: 100%;
    padding: 15px;
}

.uppy-Webcam-permissons p {
    max-width: 450px;
    margin: 0;
    color: rgba(var(--bm-text-color), 0.4);
    line-height: 1.3;
    line-height: 1.45;
    text-align: center;
}

.uppy-Webcam-permissonsIcon svg {
    width: 100px;
    height: 75px;
    margin-bottom: 30px;
    color: rgba(var(--bm-text-color), 0.3);
}

.uppy-Webcam-title {
    max-width: 500px;
    margin: 0;
    margin-bottom: 5px;
    padding: 0 15px;
    color: rgba(var(--bm-text-color), 1);
    font-weight: 400;
    font-size: 22px;
    line-height: 1.35;
    text-align: center;
}
